@import url(https://fonts.googleapis.com/css2?family=East+Sea+Dokdo&family=Noto+Sans+KR:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poor+Story&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body{
  background: rgb(192,233,8);
background: linear-gradient(90deg, rgba(192,233,8,0.6292892156862745) 30%, rgba(0,212,255,0.5480567226890756) 100%);
  overflow-x: hidden;
  background-repeat:no-repeat;
  background-attachment: fixed;
}

html{
  scroll-behavior: smooth;
}

@-webkit-keyframes fadeout {
  from {opacity: 1;}
  to {opacity: 0;}
}

@keyframes fadeout {
  from {opacity: 1;}
  to {opacity: 0;}
}

@-webkit-keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@-webkit-keyframes leftnavbar {
  from { -webkit-transform: translateX(-100%); transform: translateX(-100%)}
  to { -webkit-transform: translateX(0); transform: translateX(0);}
}

@keyframes leftnavbar {
  from { -webkit-transform: translateX(-100%); transform: translateX(-100%)}
  to { -webkit-transform: translateX(0); transform: translateX(0);}
}

@-webkit-keyframes big {
  0% {box-shadow: 0 0 5px 0px rgb(255, 217, 0);}
  50% {box-shadow: 0 0 15px 0px rgb(255, 217, 0);}
  100% {box-shadow: 0 0 5px 0px rgb(255, 217, 0);}
}

@keyframes big {
  0% {box-shadow: 0 0 5px 0px rgb(255, 217, 0);}
  50% {box-shadow: 0 0 15px 0px rgb(255, 217, 0);}
  100% {box-shadow: 0 0 5px 0px rgb(255, 217, 0);}
}

@-webkit-keyframes rainbow {
  0% {box-shadow: 0 0 5px 0px rgb(255, 217, 0);}
  10% {box-shadow: 0 0 7px 0px rgb(0, 255, 0);}
  20% {box-shadow: 0 0 8px 0px rgb(0, 255, 213);}
  30% {box-shadow: 0 0 10px 0px rgb(68, 0, 255);}
  40% {box-shadow: 0 0 15px 0px rgb(225, 0, 255);}
  50% {box-shadow: 0 0 20px 0px rgb(0, 255, 0);}
  60% {box-shadow: 0 0 15px 0px rgb(255, 60, 0);}
  70% {box-shadow: 0 0 10px 0px rgb(0, 255, 136);}
  80% {box-shadow: 0 0 8px 0px rgb(255, 0, 64);}
  90% {box-shadow: 0 0 7px 0px rgb(255, 230, 0);}
  100% {box-shadow: 0 0 5px 0px rgb(0, 47, 255);}
}

@keyframes rainbow {
  0% {box-shadow: 0 0 5px 0px rgb(255, 217, 0);}
  10% {box-shadow: 0 0 7px 0px rgb(0, 255, 0);}
  20% {box-shadow: 0 0 8px 0px rgb(0, 255, 213);}
  30% {box-shadow: 0 0 10px 0px rgb(68, 0, 255);}
  40% {box-shadow: 0 0 15px 0px rgb(225, 0, 255);}
  50% {box-shadow: 0 0 20px 0px rgb(0, 255, 0);}
  60% {box-shadow: 0 0 15px 0px rgb(255, 60, 0);}
  70% {box-shadow: 0 0 10px 0px rgb(0, 255, 136);}
  80% {box-shadow: 0 0 8px 0px rgb(255, 0, 64);}
  90% {box-shadow: 0 0 7px 0px rgb(255, 230, 0);}
  100% {box-shadow: 0 0 5px 0px rgb(0, 47, 255);}
}

body {
  font-family: 'Noto Sans KR', sans-serif;
}

.App {
  display: -webkit-flex;
  display: flex;
  height: 100vh;
}

/* start button */
.gotrip {
  text-align: center;
  margin: auto;
  border-radius: 15px;
  background-color: white;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.459);
  cursor: pointer;
  font-family: 'East Sea Dokdo', cursive;;
  font-size: 15vmin;
  padding: 10px 25px;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
  z-index: 100;
}

/* day button */
.daybuttons {
  display: none;
}

.daybuttons.active {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: space-around;
          align-content: space-around;
  margin: auto;
  z-index: 99;
}

.btn {
  font-family: 'East Sea Dokdo', cursive;;
  border-radius: 15px;
  background-color: white;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.459);
  cursor: pointer;
  font-size: 10vmin;
  padding: 15px 30px;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
  margin: 20px 0;
}


/* main contents */
.container {
  display: none;
}

.container.active {
  display: block;
  transition: .3s ease-in-out;
  width: 100vw;
}


/* left side bar */
.leftnavi{
  font-family: 'East Sea Dokdo', cursive;;
  position: fixed;
  top: 0;
  left: 0;
  width: 0;
  height: 100vh;
  background-color: rgba(136, 182, 11, 0.192);
  overflow: hidden;
  display: block;
  transition: .3s ease-in-out;
  z-index: 3;
}

.leftnavi a{
  height: 20px;
  text-align: center;
  border-radius: 5px;
  margin: 40px 30px;
  text-decoration: none;
  font-size: 25px;
  color: #353333;
  display: block;
  overflow: hidden;
  border-bottom: 1px rgba(0, 0, 0, 0.205) solid;
  padding-bottom: 10px;
  text-shadow: 0 0 10px rgba(172, 255, 47, 0.541);
}


/* top side bar */
.navibutton {
  display: none;
  position: fixed;
  background-image: url("https://image.flaticon.com/icons/png/512/660/660376.png");
  background-color: white;
  border-radius: 100%;
  margin: 10px;
  width: 30px;
  height: 30px;
  background-size: cover;
  top:0px;
  left: 0px;
}

.navibutton.on {
  display: block;
}

.topnavi {
  display: none;
  position: fixed;
  top: 10px;
  right: 10px;
  padding: 8px;
  border-radius: 10px;
  background-color: white;
  z-index: 3;
}


.gotrip.active {
  display: none;
}


/* list container */
.list{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-animation: fadein 1s linear;
          animation: fadein 1s linear
}

.list_li {
  margin : 8px 20px;
  border-radius: 10px;
  padding : 10px 5px;
  background-color: white;
  box-shadow: 0 0 5px 0px rgba(128, 128, 128, 0.514);
  cursor: pointer;
}


.list_li.active {
  -webkit-animation: rainbow 30s infinite ;
          animation: rainbow 30s infinite ;
  transition: 1s;
}

.list_ul {
  pointer-events: none;
}

.list_ul li {
  pointer-events: none;
  padding: 5px;
}

.list_ul li:last-child {
  pointer-events: all;
}
.list_ul li:nth-child(2),
.list_ul li:nth-child(3),
.list_ul li:nth-child(4),
.list_ul li:nth-child(5){
  display: none;
}

.list_li.active li:nth-child(2),
.list_li.active li:nth-child(4),
.list_li.active li:nth-child(5){
  display: -webkit-flex;
  display: flex;
  border-top:1px solid rgba(128, 128, 128, 0.356);
  font-size: 13px;
}

.list_num{
  font-family: 'Poor Story', cursive;
  color: #353333;
  display: -webkit-flex;
  display: flex;
  font-size: 20px;
  font-weight: bolder;
}

.list_name{
  color: black;
  font-size:18px;
  margin: auto;
  
}

.list_ul a{
  color: black;
}

.daylist {
  margin: 10px 0;
  font-weight: bolder;
  font-family: 'Poor Story', cursive;
  font-size: 25px;
  text-align: center;
  padding: 10px 0;
  border-top: 1px solid rgba(128, 128, 128, 0.521);
  border-bottom: 1px solid rgba(128, 128, 128, 0.452);
}

.air {
  display: none;
}

.air.active img {
  top: 50vh;
  left: 50vw;
  position: absolute;
  -webkit-transform: translate(-50% ,-50%);
          transform: translate(-50% ,-50%);
  -webkit-animation: airplane 3s ease-in-out;
          animation: airplane 3s ease-in-out;
  z-index: -3;
}

@-webkit-keyframes airplane {
  0% {-webkit-transform : translate(-80vw, 80vh);transform : translate(-80vw, 80vh); }
  40% {-webkit-transform : translate(0);transform : translate(0); }
  80% {-webkit-transform : translate(0);transform : translate(0); opacity: 1; }
   100%{-webkit-transform : translate(50vw, -80vh);transform : translate(50vw, -80vh); opacity: 0;}
  
}

@keyframes airplane {
  0% {-webkit-transform : translate(-80vw, 80vh);transform : translate(-80vw, 80vh); }
  40% {-webkit-transform : translate(0);transform : translate(0); }
  80% {-webkit-transform : translate(0);transform : translate(0); opacity: 1; }
   100%{-webkit-transform : translate(50vw, -80vh);transform : translate(50vw, -80vh); opacity: 0;}
  
}


.air.active {
  width: 100vw;
  height: 100vh;
  display :-webkit-flex;
  display :flex;
  position: fixed;
  transition: 1s;
  z-index: -3;

}

@media screen and (min-width:600px) {
  .container.active, .gotrip  {
    width: 500px;
    margin-left:50vw;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}
